    import React,{Component} from 'react';
    import axios from 'axios';
    import TensorflowExample from './tensorflow';
    import StockUtils from '../StockManagement/StockUtils';
    import { Col, Row, Button, Form, FormGroup, Label, Input } from 'reactstrap';
    import Chart from '../Components/MainChartComponent';
    
    // const api=axios.create({
    //     baseURL:`http://localhost:3000/predict/value`
    // })
    
    class AiPrediction extends Component {
        constructor(props){
            super(props)
            this.state={
                stockName:'',
                x_val:[],
                y_val:[]
                
            }
        }


        componentDidMount(){
            
            this.Stocker();
        }
        Stocker(){
            const pointerToThis =this;
            // console.log(pointerToThis);
            const API_Key='X662IPBO4D3M25MX';
            const Stock_Symbol=this.state.stockName;
            console.log("my stoick".Stock_Symbol);
            //we are using backtick so that we can copy API directly here by putting in it
            let API_Call=`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${Stock_Symbol}&outputsize=compact&apikey=${API_Key}`;
            let stocksXval=[];
            let stocksYval=[];
            let presentDate=[];
            let wholeHigh=[];
            let wholeLow=[];
            let wholeClose=[];
            
            fetch(API_Call)
                .then(
                    function(
                        response){
                            return response.json();
                    })
                .then(
                    function(data){
                        var x=1;
                        console.log(data);
                        // this.setState({loading:true});
                        for (var key in data['Time Series (Daily)']){
                            stocksXval.push(key);
                            stocksYval.push(
                                data['Time Series (Daily)'][key]['1. open']
                                );
                                wholeHigh.push(
                                    data['Time Series (Daily)'][key]['2. high']
                                    );
                                    wholeLow.push(
                                        data['Time Series (Daily)'][key]['3. low']
                                        );
                                        wholeClose.push(
                                            data['Time Series (Daily)'][key]['4. close']
                                            );
                                
                                if(x>=1){
                                    presentDate.push(key)
                                        x=x-1
                                }
                        }
                        
    
                        console.log('dates are here',stocksXval);
                        pointerToThis.setState({ 
                            x_val:stocksXval,
                            y_val:stocksYval,
                            presentDate:presentDate,
                            wholeHigh:wholeHigh,
                            wholeLow:wholeLow,
                            wholeClose:wholeClose,
                            loading:false,
                        })
                    }
                )
        }
        handleChange=(event)=>{
        
        
            var final=event.target.value
            var i;
            var index;
            for(i=0;i<final.length;i++){
            if(final[i]==='-'){
                index=i;
                break;
            }
        }
            this.setState({stockName:final.substring(index+1)},() => {
                this.Stocker()})
            console.log('My name is anthony',final.substring(index+1));
        }
        onButtonClick(){
            console.log("button is clicked")
            fetch('http://127.0.0.1:5000')
                .then(function(response){
                    return response.json();
                })

        }

        

        // prediction(){
        //     fetch("api")
        //     .then(
        //         (
        //             response)=>{
        //                 return response.json();
        //         })
        //         .then(function(data){
        //             console.log(data);
        //         })
        // }

    render() {
        return (
            <div>
            <div className="jumbotron bg-dark text-light">
                    <div className="container">
                        <h2 className="display-3">Stockfaq</h2>
                            <p className="lead">An AI based Stock predicting model.</p>
                                
                                    
                    </div>
                    </div>
            <div className="container" style={{color: 'white'}}>


                <Form>
                <Row form>
            <Col md={12}>
            <FormGroup>
            <div className="col input-group">
                                        <Input className="inputBrand" type="select" name="carbrand" id="carBrand" onChange={this.handleChange} style={{background_color:'red'}} >
                                            <option>Please select a stock</option>
                                            {StockUtils.map((data) => {
                                            return <option key={data.id}>{data.name}-{data.symbol}</option>
                                            })}
                                        </Input>
                                        <Button onClick={() =>this.onButtonClick()}>Press to get data</Button>
                                    </div>
                                    
            </FormGroup>
            </Col>
        </Row>
        </Form>
        <Chart x_axis={this.state.x_val} y_axis={this.state.y_val}   />
            <TensorflowExample></TensorflowExample>
            </div>
                                            
            </div>
            
        )
    }
    }
        


    export default AiPrediction;
