    import React, { useState } from 'react';
    import {
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
    CarouselCaption
    } from 'reactstrap';

    const items = [
    {
        src: 'https://media.gettyimages.com/photos/financial-stock-market-numbers-and-city-light-reflection-picture-id942696884?s=2048x2048',
        altText: 'Slide 1',
        caption: 'Slide 1'
    },
    {
        src: 'https://media.gettyimages.com/photos/closeup-of-stock-market-data-on-digital-display-picture-id1058454392?s=2048x2048',
        altText: 'Slide 2',
        caption: 'Slide 2'
    },
    {
        src: 'https://media.gettyimages.com/photos/stock-market-report-with-bull-and-bear-picture-id157562003?s=2048x2048',
        altText: 'Slide 3',
        caption: 'Slide 3'
    }
    ];

    const MyCarousel = (props) => {
    const [activeIndex, setActiveIndex] = useState(0);
    const [animating, setAnimating] = useState(false);

    const next = () => {
        if (animating) return;
        const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
        setActiveIndex(nextIndex);
    }

    const previous = () => {
        if (animating) return;
        const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
        setActiveIndex(nextIndex);
    }

    const goToIndex = (newIndex) => {
        if (animating) return;
        setActiveIndex(newIndex);
    }

    const slides = items.map((item) => {
        return (
        <CarouselItem
            onExiting={() => setAnimating(true)}
            onExited={() => setAnimating(false)}
            key={item.src}
        >
            <img src={item.src} alt={item.altText} />
            <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
        </CarouselItem>
        );
    });

    return (
        
        <Carousel
        style={{height:'500', width:'500'}}
        activeIndex={activeIndex}
        next={next}
        previous={previous}
        >
        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
        {slides}
        <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
        <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
        </Carousel>
        
    );
    }

    export default MyCarousel;